body {
  font-family: 'Helvetica Neue';
  font-weight: 100;
}

.fa-btn {
  margin-right: 6px;
}

.alert {
  text-align: center;
}

.alert--info {
  background: gray;
}

.alert--success {
  background: lime;
}

.alert--error {
  background: red;
}

.nav>li>a>img {
  border-radius: 50px;
  background-color: white;
  height: 50px;
}
